<template>
	<div class="pageWrap navBarPadding" :style='"min-height:"+bgWrapHeight+"px;"'>
        <div class="navBarBox" ref="navBarHeight">
            <img class='navLeftArrowIcon' src="../assets/images/headerBack.png" alt="" @click='goBack()'>
            <p class='navTitle'>Recharge</p>
            <div class="libaryHeaderRightBox" @click="goPage('/BalanceHistory')">
                History
            </div>
        </div>
        <div class="classTypeBox" style="display: none;">
            <div class="classTypeItem"  @click="selectRechargeType('Cash')">
                <p class="classTypeName" :style="selectRechargeTypeId == 'Cash' ? 'color:#FF30DF;':''">By Cash</p>
                <span class="classSelectBorder" v-if="selectRechargeTypeId == 'Cash'"></span>
            </div>
            <div class="classTypeItem"  @click="selectRechargeType('USDT')">
                <p class="classTypeName" :style="selectRechargeTypeId == 'USDT' ? 'color:#FF30DF;':''">By USDT</p>
                <span class="classSelectBorder" v-if="selectRechargeTypeId == 'USDT'"></span>
            </div>
        </div>
        <div class="orderDetailWrap" v-if="rechargeData">
            <div class="balanceTopBox">Current Balance: {{moneySymbol+ (balance ? balance : '--')}}</div>
            <div class="mainWhiteBox">
                <div class="cashMainBox">
                    <p class="willReceive">Will receive</p>
                    <p class="willReceiveNumber">{{moneySymbol+willReceive}}</p>
                </div>
                <p class="rechargeAmount" style="margin-bottom:.18rem;">Recharge amount</p>
                <template v-if="rechargeData.bank.custom.enable">
                    <div class="inputWrapBox">
                        <div class="subscribeInputBox flexBetween">
                            <span class='moneySymbol'>{{moneySymbol}}</span>
                            <input class='subscribeInput' type="text" v-model="inputMoney" @input="checkInput(inputMoney)">
                        </div>
                        <div class="rechargeAlertBox">
                            <p v-if="inputCheckAlert" style="color: #FF274C;">{{inputCheckText}}</p>
                            <p v-else>Single recharge limit is {{rechargeData.bank.custom.min}} min. and {{rechargeData.bank.custom.max}} max.</p>
                            <p>Fee: {{parseFloat((rechargeData.bank.fee.rate/1*100).toFixed(2))}}% when ≥ {{moneySymbol+rechargeData.bank.fee.condition}}, {{rechargeData.bank.fee.fix}} when &#60; {{moneySymbol+rechargeData.bank.fee.condition}}.</p>
                        </div>
                    </div>
                    <p class="rechargeAmount" style="margin-top: .36rem;margin-bottom: .26rem;">Recommended</p>
                </template>
                <div class="moneyDefaultBox flexStart" v-if="rechargeData.bank.common && rechargeData.bank.common.length > 0">
                    <div class="moneyItem" :class='selectMoneyIndex === index ?"selectActive":""' :style="(index+1)%3 == 0 ? 'margin-right: 0;':''" v-for="(item,index) in rechargeData.bank.common" :key="index" @click="selectMoneyItem(item,index)">{{moneySymbol+item}}</div>
                </div>
                <div class="submitBtn" :class='willReceive != "N/A" && willReceive != "--" && !inputCheckAlert ? "GradientAllowBtn":""' style="margin-top: .27rem;" @click="submitRecharge()">Submit</div>
            </div>
        </div>
        <van-dialog class='alertModelBox' v-model="pamentBox" :showConfirmButton='false'>
            <template #default>
                <div class="alertModelTitBox">
                    Note
                </div>
                <div class="alertBoxMain">
                    <p class="alertContent">Please return after payment is completed.</p>
                    <div class="btnBox flexBetween" style="margin-top: .57rem;">
                        <a class="submitBtn_91 blueAllowBtn" target="_blank" :href="payUrl" style="width: 100%;"  @click="pamentBox =false">Continue</a>
                    </div>
                </div>
            </template>
        </van-dialog>
	</div>
</template>
<script>
	import https from '../api/https'
	export default {
		name: 'Recharge',
		data: function(){
			return {
                showLoading: true,
				bgWrapHeight: 200,
                fixNumber: https.fixNumber,
                moneySymbol: https.moneySymbol,

                payUrl: '',
                pamentBox: false,

                rechargeData: '',
                selectRechargeTypeId: 'Cash',
                selectMoneyIndex: '',
                selectMoneyNumber: '',
                inputMoney: '',
                inputCheckAlert: false,
                inputCheckText: '',
                balance: '',
			}
		},
		created(){
			this.getData()
            this.getAccountBalance()
            if(this.$route.params.VHinvite_code && this.$route.params.VHinvite_code != 'undefined') {
				localStorage.setItem('VHinvite_code',this.$route.params.VHinvite_code)
				this.VHinvite_code = this.$route.params.VHinvite_code
			}else if(localStorage.getItem('VHinvite_code') && localStorage.getItem('VHinvite_code') != 'undefined') {
				this.VHinvite_code = localStorage.getItem('VHinvite_code')
			}else {
                this.VHinvite_code = ''
				localStorage.setItem('VHinvite_code','')
            }
		},
		mounted: function () {
			let viwePortHeight = document.documentElement.clientHeight || document.body.clientHeight
			let navBarHeight = this.$refs.navBarHeight.offsetHeight
			this.bgWrapHeight = viwePortHeight - navBarHeight
		},
		methods: {
			getData: function(){
                this.showLoading = true
				setTimeout(() => {
                    if(this.showLoading) {
                        this.$toast.loading({
                            message: 'Loading...',
                            forbidClick: true,
                            loadingType: 'spinner',
                            icon: require('../assets/images/loadingIcon.png'),
                            duration: 0,
                            className:'toastLoading'
                        });
                    }
                }, 500);
                let params = {
                    type: 'recharge'
                }
				https.fetchGet('/api/pay/cashier',params).then((data) => {
                    this.showLoading = false
					this.$toast.clear()
					let res = data.data
					if(res.code == 0) {
						this.rechargeData = res.data
					}else if(res.code == -2 || res.code == -3) {
						this.goPage("/Login")
					}else {
						this.$toast({
							message: res.msg,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
					}
				}).catch(err=>{
					console.log(err)
					if(this.NetWorkRetryTimes && this.NetWorkRetryTimes >= 1) {
                        this.showLoading = false
						this.$toast({
							message: https.NetworkErrorText,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
						this.NetWorkRetryTimes = 0
					}else {
						this.NetWorkRetryTimes = 0
						this.NetWorkRetryTimes++
						this.getData()
					}
				})
			},
            getAccountBalance: function(){
				this.showLoading = true
                setTimeout(() => {
                    if(this.showLoading) {
                        this.$toast.loading({
                            message: 'Loading...',
                            forbidClick: true,
                            loadingType: 'spinner',
                            icon: require('../assets/images/loadingIcon.png'),
                            duration: 0,
                            className:'toastLoading'
                        });
                    }
                }, 500);
				https.fetchGet('/api/user/account' ).then((data) => {
					this.showLoading = false
					this.$toast.clear()
					let res = data.data
					if(res.code == 0) {
						this.balance = res.data.balance
                        
					}else if(res.code == -2 || res.code == -3) {
						this.goPage("/Login")
					}else {
						this.$toast({
							message: res.msg,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
					}
					console.log(data)
				}).catch(err=>{
					console.log(err)
					if(this.NetWorkRetryTimes && this.NetWorkRetryTimes >= 1) {
                        this.showLoading = false
						this.$toast({
							message: https.NetworkErrorText,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
						this.NetWorkRetryTimes = 0
					}else {
						this.NetWorkRetryTimes = 0
						this.NetWorkRetryTimes++
						this.getAccountBalance()
					}
				})
			},
            submitRecharge(){
                if(this.willReceive == 'N/A' || this.willReceive == '--' || this.inputCheckAlert) {
                    return
                }
                this.showLoading = true
                setTimeout(() => {
                    if(this.showLoading) {
                        this.$toast.loading({
                            message: 'Preparing...',
                            forbidClick: true,
                            loadingType: 'spinner',
                            icon: require('../assets/images/loadingIcon.png'),
                            duration: 0,
                            className:'toastLoading'
                        });
                    }
                }, 500);
                let params = {
                    type: 'recharge',
                    money: this.inputMoney
                }
				https.fetchPost('/api/pay/cashier/create',params).then((data) => {
					this.$toast.clear()
                    this.showLoading = false
					let res = data.data
					if(res.code == 0) {
                        this.payUrl = res.data.openUrl
                        this.pamentBox = true

					}else if(res.code == -2 || res.code == -3) {
						this.goPage("/Login")
					}else {
						this.$toast({
							message: 'Unable to\ninitiate payment',
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
					}
				}).catch(err=>{
                    this.showLoading = false
                    console.log(err)
                    this.$toast({
                        message: https.NetworkErrorText,
                        forbidClick: true,
                        icon: require('../assets/images/errorIcon.png'),
                        duration: 2000,
                        className: 'resultToast'
                    });
				})
            },
            selectRechargeType(item){
                this.selectRechargeTypeId = item
            },
            selectMoneyItem(item,index) {
                this.inputMoney = item
                this.selectMoneyIndex = index
            },
            checkInput(inputMoney){
                let rge = /^[0-9]+$/g;
                if(inputMoney/1 < this.rechargeData.bank.custom.min/1){
					this.inputCheckAlert = true
					this.inputCheckText = 'Single recharge must ≥'+this.moneySymbol+this.rechargeData.bank.custom.min+'.'
                }else if(!rge.test(inputMoney)) {
                    this.inputCheckAlert = true
					this.inputCheckText = 'Recharge amount cannot contain decimals.'
                } else if(inputMoney/1 > this.rechargeData.bank.custom.max/1){
					this.inputCheckAlert = true
					this.inputCheckText = 'Recharge amount exceeds the limit.'
                }else {
                    this.inputCheckAlert = false
					this.inputCheckText = ''
                }
            },
			goPage(page){
                if(this.VHinvite_code) {
					page = page +'/'+ this.VHinvite_code
				}
				this.$router.push(page)
			},
			goBack(){
				this.$router.go(-1)
			},
		},
        computed: {
            willReceive(){
                if(this.inputMoney) {
                    if(this.rechargeData.bank.custom.enable) {
                        if(this.inputMoney/1 < this.rechargeData.bank.custom.min/1 || this.inputMoney/1 > this.rechargeData.bank.custom.max/1) {
                            console.log(this.inputMoney)
                        console.log(222)
                            return 'N/A'
                        }else {
                        console.log(333)
                            if(this.inputMoney/1 < this.rechargeData.bank.fee.condition/1) {
                                return parseFloat((this.inputMoney/1 - this.rechargeData.bank.fee.fix).toFixed(this.fixNumber))
                            }else {
                                return parseFloat((this.inputMoney/1 - ((this.inputMoney/1) * this.rechargeData.bank.fee.rate)).toFixed(this.fixNumber))
                            }
                        }
                    }else {
                        if(this.inputMoney/1 < this.rechargeData.bank.fee.condition/1) {
                            return parseFloat((this.inputMoney/1 - this.rechargeData.bank.fee.fix).toFixed(this.fixNumber))
                        }else {
                            return parseFloat((this.inputMoney/1 - ((this.inputMoney/1) * this.rechargeData.bank.fee.rate)).toFixed(this.fixNumber))
                        }
                    }
                }else {
                    return '--'
                }
            },
        },
        destroyed() {
            clearInterval(this.timer2)
        }
	}
</script>
